const thorui = {
	getClipboardData: function(data, callback) {

		uni.setClipboardData({
			data: data,
			success(res) {
				uni.getClipboardData({
					success(res) {
						("function" == typeof callback) && callback(true)
					},
					fail(res) {
						("function" == typeof callback) && callback(false)
					}
				})
			},
			fail(res) {
				("function" == typeof callback) && callback(false)
			}
		})





















	}
};

module.exports = {
	getClipboardData: thorui.getClipboardData
};
